.contentBanner {
  object-fit: cover;
  width: 100%;
  height: 310px;
}
.wrapperAvatar {
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
}
.avatar {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.profileInfo {
  display: grid;
  grid-template-columns: 2fr 10fr;
  gap: 10px;
  margin-top: 8px;
  min-height: 170px;
}
.profileDescr {
  display: flex;
  align-items: center;
  justify-content: center;
}
