.wrapperPagination{
    display: flex;
    align-items: center;
    gap: 3px
}
.selectedPage{
    font-size: 18px;
    padding: 0 5px;
    position: relative;
}
.selectedPage:after{
    content: '';
    position: absolute;
    top: 1px;
    left: 2px;
    right: 2px;
    bottom: 1px;
    border: 1px solid #d1cfcf;
    border-radius: 3px;
}